import React from 'react'

interface InputProps {
    type: 'text' | 'email' | 'password' | 'number';
    placeholder: string,
    value: string,
    className?: string;
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void
}

const Input = ({ type, placeholder, value, className, onChange }: InputProps) => {
    return (
        <div className="w-full">
            <input
                type={type}
                placeholder={placeholder}
                value={value}
                onChange={onChange}
                className={`w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:border-[#7842BF] ${className}`}
            />
        </div>
    )
}

export default Input